.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  justify-content: space-around;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  white-space: nowrap;
  text-transform: uppercase;
  cursor: pointer;
  color: #060b35;
  justify-content: center;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  margin-left: 0;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: flex;
  position: absolute;
  bottom: 0;
}

.catalog__card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 200px;
  padding: 0 16px;
  transition: all .3s;
  display: flex;
}

.catalog__card-img {
  width: 160px;
  height: 134px;
  margin-top: 32px;
  margin-bottom: 40px;
}

.catalog__card-title {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.catalog__card-product-code {
  color: #616070;
  align-self: flex-start;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.catalog__card-price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  line-height: 18px;
  display: flex;
}

.catalog__card-price-title {
  color: #616070;
  font-size: 12px;
}

.catalog__card-price-value {
  font-size: 16px;
  font-weight: 700;
}

.catalog__card-buy-button {
  box-sizing: border-box;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin: 16px 0;
  font-size: 14px;
  font-weight: 700;
  display: flex;
}

.catalog__card-buy-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.catalog__card:hover {
  transform: scale(1.2);
}

.catalog__card:hover .catalog__card-title {
  color: #34568b;
}

.stars {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 24px;
  display: flex;
}

.stars__items {
  gap: 4px;
  display: flex;
}

.stars__items-star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  justify-content: center;
  width: 16px;
  height: 16px;
}

.stars__items--1 .stars__items-star:nth-child(-n+1), .stars__items--2 .stars__items-star:nth-child(-n+2), .stars__items--3 .stars__items-star:nth-child(-n+3), .stars__items--4 .stars__items-star:nth-child(-n+4), .stars__items--5 .stars__items-star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__review-title {
  text-align: right;
  font-size: 10px;
  line-height: 14px;
}

body {
  margin: 0;
}

html {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  align-items: start;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}
